import { reactive, toRefs } from 'vue';
import { useStore } from 'vuex';
import Son from './Son.vue';
export default {
  name: 'ComB',
  components: {
    Son
  },

  setup() {
    const store = useStore();
    const state = reactive({
      num: 0
    });
    const data = store.state;

    function handleClk() {
      store.commit('add', {
        a: 1
      });
    }

    function handleAddAsync() {
      store.dispatch('addAsync');
    }

    function handleNum() {
      state.num++;
    }

    function d() {
      handleNum();
    }

    return {
      data,
      ...toRefs(state),
      handleClk,
      handleAddAsync,
      handleNum,
      d
    };
  }

};